import axios from 'axios';
import Cookies from 'js-cookie';
import { getCookieFromReq } from '../helper/utils';

const setAuthHeader = (req) => {
    const token = req ? getCookieFromReq(req, 'jwt') : Cookies.getJSON('jwt');

    if(token){
        return { headers: {'authorization': `Bearer ${token}`} };
    }
}

export const getSecretData = async () => {
    return await axios.get('/api/v1/secret/', setAuthHeader()).then(response => response.data); // 1-liner code doesn't require 'return' to be mentioned
}

export const getSecretDataServer = async (req) => {
    return await axios.get('http://localhost:4000/api/v1/secret/', setAuthHeader(req)).then(response => response.data); // 1-liner code doesn't require 'return' to be mentioned
}